@charset "UTF-8";
#shared-controls label[for=button-fullscreen], .loading header .logo {
  text-indent: -9999px;
  overflow: hidden;
  color: transparent;
  text-shadow: none;
  white-space: pre;
  font-size: 0;
  background-position: center center;
  justify-content: center;
}

html, body, h1, h2, h3, h4, h5, h6, p {
  margin: 0;
  padding: 0;
}

:root {
  --curve:12px;
  --pad:2vmin;
  --footer-gap:calc(61px);
  --ease-s:450ms;
  --ease-m:90ms;
  --ease-q:45ms;
  --duration:calc( 2 * var(--ease-s) );
  --duration-scroll:calc( 5 * var(--ease-s) );
  --overlap:0.2;
  --offset:0.1s;
}

html, input, button, textarea, option, select {
  font-family: "Oxanium", sans-serif !important;
  font-weight: 800;
}

html {
  height: -webkit-fill-available;
  text-rendering: optimizeSpeed;
  transition: background-color var(--ease-s) linear;
  color: #e7dbd5;
  background-color: #262422;
  background-image: linear-gradient(rgba(38, 36, 34, 0.5), rgba(38, 36, 34, 0.1));
  text-rendering: optimizeSpeed;
}
html.loading {
  background-color: #e7dbd5;
  background-image: linear-gradient(rgba(255, 255, 255, 0.9), rgba(255, 255, 255, 0.1));
}

body {
  min-height: -webkit-fill-available;
  min-height: 100vh;
}

html.loading body {
  background-image: url(./assets/textures/45-degree-fabric-light.png);
  background-image: url(./assets/textures/45-degree-fabric-dark.png);
}

body {
  overflow: hidden;
  display: flex;
}

h1 strong, h2, h3, h4, h5, h6 {
  font-weight: 800;
}

h1 {
  font-weight: 200;
}

label, select, option {
  font-size: 1rem;
  user-select: none;
}

p {
  font-weight: 500;
}

p[aria-label=Summary] {
  color: transparent;
  text-shadow: none;
}

small {
  font-size: 60%;
  font-weight: 200;
}

a {
  color: #455b63;
  text-decoration: none;
}

header {
  position: absolute;
  z-index: -1;
  top: 0;
  right: 0;
  left: 0;
  bottom: 5rem;
  display: grid;
  text-align: center;
  justify-content: center;
}
header h1 {
  background-color: #262422;
  color: #877b78;
  display: inline-block;
  padding: var(--pad) calc(var(--pad) * 2);
  text-transform: uppercase;
  align-self: self-start;
  justify-self: center;
  margin-top: var(--pad);
  transition: color var(--ease-s) ease-out;
  font-size: clamp(2rem, -0.5385rem + 7.6923vw, 3.5rem);
  text-shadow: rgba(0, 0, 0, 0.56) 0 1px 4px, rgba(0, 0, 0, 0.56) 0 2px 4px, rgba(0, 0, 0, 0.56) 0 3px 4px, rgba(255, 255, 255, 0.96) 0 0 4px;
}
header h1 strong {
  color: #455b63;
}
header h2, header p {
  text-shadow: rgba(255, 255, 255, 0.96) 0 1px 4px, rgba(255, 255, 255, 0.7) 0 2px 4px, rgba(255, 255, 255, 0.5) 0 3px 4px, rgba(157, 130, 114, 0.56) 0 0 4px;
}
header h2 {
  color: #ab8f54;
  margin-top: auto;
}
header .logo {
  align-self: flex-end;
  z-index: -2;
}

.logo {
  margin-left: auto;
  margin-right: auto;
  display: flex;
  flex-direction: column;
  fill: currentColor;
  transition: fill var(--ease-m) linear;
  width: 100%;
  min-height: 10vh;
  max-height: 90vmin;
  max-width: 90vmin;
  color: transparent;
  visibility: visible;
  z-index: 1;
}
.logo svg {
  order: -1;
}

main {
  display: flex;
  flex-direction: column;
  align-items: center;
  justify-content: center;
  margin: auto;
}

option, select {
  text-align: center;
}

select {
  appearance: none;
  padding: var(--pad);
}

video, canvas {
  max-width: 100%;
  visibility: hidden;
}

label#progress-bar {
  position: absolute;
  width: 100%;
  bottom: 10%;
  color: #5d493c;
  outline: none;
  border: 0;
  display: flex;
  flex-direction: column;
}
label#progress-bar:hover {
  background-color: transparent;
}
label#progress-bar #progress {
  margin: auto;
}

#app-frame {
  --person-a-x:0;
  --person-a-y:0;
  --person-b-x:0;
  --person-b-y:0;
  --wig:0;
  --hue:180;
  --l:20%;
  position: relative;
  transition: opacity var(--ease-s) ease-out;
  display: block;
}
#app-frame::before, #app-frame::after {
  transition: var(--ease-s) linear opacity;
  opacity: 0;
}
#app-frame:before {
  animation: Fade var(--ease-s) linear 0s 1 normal both;
  opacity: 0.9;
  position: absolute;
  top: -4px;
  right: 5px;
  width: 10%;
  display: block;
  z-index: 3;
  color: pink;
  content: url(./assets/logos/interface_colour.svg);
  content: url(./assets/logos/interface_current-color.svg);
}

@media (any-hover: hover) {
  #controls {
    animation: Fade var(--ease-s) linear 1500ms both reverse;
  }

  html:focus-visible #controls,
html:focus-within #controls,
html:hover #controls {
    animation: Fade var(--ease-q) linear 0s both;
  }

  html.debug #controls {
    animation: none;
  }
}
.loading h1 strong,
p strong {
  animation: TextColours 4s linear infinite 0.05s both;
}

.loading {
  --loader-size:8vmin;
  --loader-duration:4;
}
.loading::before, .loading::after {
  content: "";
  opacity: 1;
  width: 100%;
  height: 100%;
  background-image: linear-gradient(blue, blue);
  background-size: var(--loader-size) var(--loader-size);
  background-position: top left;
  background-repeat: no-repeat;
  position: absolute;
  top: 0;
  left: 0;
  display: block;
  animation: Loading calc(var(--loader-duration) * 1s) 0s infinite cubic-bezier(0.68, -0.55, 0.265, 1.55);
}
.loading::after {
  animation-delay: calc( var(--loader-duration) / 4 * 0.02s );
  filter: blur(5px);
}
.loading header > * {
  max-height: 100vh;
  grid-area: 1/1/1/1;
  visibility: visible;
  animation: Fade var(--ease-s) ease-in 1 0s both;
}
.loading header:nth-child(1) {
  animation-delay: calc( var(--offset) + 0 * var(--overlap) * var(--duration) );
}
.loading header:nth-child(2) {
  animation-delay: calc( var(--offset) + 1 * var(--overlap) * var(--duration) );
}
.loading header:nth-child(3) {
  animation-delay: calc( var(--offset) + 2 * var(--overlap) * var(--duration) );
}
.loading header:nth-child(4) {
  animation-delay: calc( var(--offset) + 3 * var(--overlap) * var(--duration) );
}
.loading header:nth-child(5) {
  animation-delay: calc( var(--offset) + 4 * var(--overlap) * var(--duration) );
}
.loading header:nth-child(6) {
  animation-delay: calc( var(--offset) + 5 * var(--overlap) * var(--duration) );
}
.loading header:nth-child(7) {
  animation-delay: calc( var(--offset) + 6 * var(--overlap) * var(--duration) );
}
.loading header:nth-child(8) {
  animation-delay: calc( var(--offset) + 7 * var(--overlap) * var(--duration) );
}
.loading header .logo {
  color: #8d3525;
  align-self: center;
  height: 100%;
  background-position: center;
  z-index: -1;
  animation: TextColours 4s linear infinite 0s both;
}
.loading header h1 {
  color: #a45c1e;
  clip-path: polygon(15% 0%, 100% 0%, 100% 100%, 0% 100%, 0% 45%);
}
.loading main {
  overflow: hidden;
  position: relative;
  pointer-events: none;
  min-height: 100vh;
}
.loading main > *:not(p) {
  visibility: hidden;
  opacity: 0;
  display: none;
}
.loading #feedback {
  text-align: center;
  bottom: var(--pad);
  top: auto;
}
.loading #shared-controls {
  visibility: hidden;
}

.player-selection header > * {
  visibility: hidden;
}
.player-selection #title {
  position: absolute;
  bottom: 0;
  visibility: hidden;
}
.player-selection #toast {
  right: auto;
  margin: 0 var(--pad);
  background-color: transparent;
  text-align: center;
}
.player-selection .person,
.player-selection #shared-controls {
  visibility: hidden;
}
.player-selection #player-selector {
  visibility: visible;
}
.player-selection #button-trio {
  display: none;
}
.player-selection fieldset > * {
  --xa:-50%;
  animation-name: Fade, Shift;
}

.loaded header {
  display: flex;
  flex-wrap: wrap;
  align-items: center;
  justify-content: flex-start;
  bottom: auto;
}
.loaded header .logo {
  margin: 0;
  min-width: 60px;
  position: absolute;
  left: 0;
  right: 0;
  top: 0;
  align-self: flex-start;
  font-size: 0;
  fill: black;
}
.loaded header .logo svg {
  width: 100vw;
  height: 100vh;
}
.loaded header h1 {
  margin: 0;
  align-self: center;
  padding-left: 0;
  opacity: 0.2;
  transition: opacity var(--ease-m) linear;
  writing-mode: vertical-rl;
  transform-origin: top left;
}
.loaded header h2 {
  margin-top: 0;
  width: 100%;
  display: block;
  opacity: 0;
}
.loaded #shared-controls {
  visibility: visible;
}
.loaded #player-selector,
.loaded #progress-bar {
  display: none;
}
.loaded #app-frame {
  border-radius: var(--curve);
  overflow: hidden;
  background-color: black;
  box-shadow: var(--wig) -1.2px 1.2px hsla(var(--hue), 90%, var(--l), 0.059), var(--wig) -2.7px 2.7px hsla(var(--hue), 90%, var(--l), 0.074), var(--wig) -4.8px 4.9px hsla(var(--hue), 90%, var(--l), 0.083), var(--wig) -8px 8.2px hsla(var(--hue), 90%, var(--l), 0.09), var(--wig) -13.2px 13.5px hsla(var(--hue), 90%, var(--l), 0.098), var(--wig) -23.1px 23.6px hsla(var(--hue), 90%, 10%, 0.112), var(--wig) -50px 51px hsla(var(--hue), 90%, 10%, 0.15), var(--wig) 1.2px 1.2px hsla(var(--hue), 90%, var(--l), 0.059), var(--wig) 2.7px 2.7px hsla(var(--hue), 90%, var(--l), 0.074), var(--wig) 4.8px 4.9px hsla(var(--hue), 90%, var(--l), 0.083), var(--wig) 8px 8.2px hsla(var(--hue), 90%, var(--l), 0.09), var(--wig) 13.2px 13.5px hsla(var(--hue), 90%, var(--l), 0.098), var(--wig) 23.1px 23.6px hsla(var(--hue), 90%, 10%, 0.112), var(--wig) 50px 51px hsla(var(--hue), 90%, 10%, 0.15);
}
.loaded #app-frame video, .loaded #app-frame canvas {
  object-fit: contain;
  visibility: hidden;
}
.loaded #app-frame canvas {
  z-index: 1;
  position: relative;
  height: auto;
  height: 100vh;
  max-width: 100vw;
}
@media (orientation: portrait) {
  .loaded #app-frame canvas {
    width: 100%;
    height: calc(var(--height) / var(--width) * 100vw );
  }
}
.loaded #app-frame video {
  transform: scaleX(-1);
  z-index: 0;
  position: absolute;
  top: 0;
  left: 0;
  width: 100%;
  height: 100%;
}
.loaded #app #title {
  visibility: hidden;
  position: absolute;
  right: 0;
}
.loaded #app-frame video,
.loaded #app-frame canvas {
  display: block;
  visibility: visible;
}
.no-midi #button-midi {
  display: none;
}

.midi-no-devices #button-midi {
  animation: BGColours 4s linear infinite 0s both;
}

main.midi #button-midi {
  display: flex;
}

main.video > img {
  display: none;
}

main.img > video {
  display: none;
}

main.active button {
  display: block;
}

p {
  color: #e7dbd5;
  pointer-events: none;
  font-weight: bold;
  text-align: right;
  display: inline-block;
  padding: var(--pad);
}

#toast {
  position: absolute;
  right: calc(var(--pad) + 2px);
  bottom: calc( var(--footer-gap) + var(--pad) );
  z-index: 909;
  color: #e7dbd5;
  background-color: rgba(38, 36, 34, 0.94);
}
#toast:empty {
  display: none;
}

#feedback {
  box-sizing: border-box;
  position: fixed;
  z-index: 303;
  top: var(--pad);
  text-align: center;
  max-width: calc(100% - 2 * var(--pad));
  color: #262422;
  background-color: #e7dbd5;
  border-radius: 3px;
  text-align: left;
  text-shadow: rgba(255, 255, 255, 0.96) 0 -1px 2px, rgba(38, 36, 34, 0.96) 0 0 2px;
}
#feedback:after {
  display: inline-block;
  content: "";
  width: 6px;
  height: calc(1rem - 0px);
  background-color: #a45c1e;
  margin-left: calc(var(--pad)/2);
  animation: Blink 1150ms 0s infinite normal both;
  vertical-align: text-bottom;
}
#feedback:empty {
  display: none;
}

#feedback:hover ~ canvas {
  opacity: 0.5;
}

#controls {
  display: block;
  margin: auto;
  padding: 0;
  overflow: auto;
  position: absolute;
  top: 0;
  left: 0;
  right: 0;
  bottom: 0;
  z-index: 303;
  /*
  	// we need media queries to complete
  	--ratio:calc( var(--width)/var(--height));
  	max-width:var(--width);
  	max-height:var(--height);
  */
}

.brand {
  background: url(./assets/logos/designerzen.svg) no-repeat left 2px center;
  background-size: contain;
  padding-left: 15px;
}

#photographs {
  position: fixed;
  bottom: var(--footer-gap);
  left: 0;
  right: 0;
  display: flex;
  overflow: auto;
  overflow-y: hidden;
  padding-top: 30px;
}
#photographs:focus-within {
  scroll-behavior: smooth;
}
#photographs a {
  color: transparent;
  user-select: none;
  font-size: 0;
  position: relative;
  box-sizing: border-box;
  transition: transform var(--ease-m) ease-out;
}
#photographs a::before {
  content: "";
  border: 4px solid #e7dbd5;
  position: absolute;
  top: 0;
  left: 0;
  right: 0;
  bottom: 0;
  opacity: 0;
  transition: var(--ease-m) linear;
}
#photographs a:hover, #photographs a:focus {
  z-index: 2;
  transform-origin: bottom center;
  transform: scale(1.2);
  background-color: #262422;
}
#photographs a:hover::before, #photographs a:focus::before {
  opacity: 1;
}
#photographs a:focus {
  outline: 4px solid #9d8272;
}
#photographs img {
  max-width: 120px;
  min-height: 44px;
  max-height: 12vmin;
  height: auto;
  width: auto;
  display: block;
}

#release {
  color: #877b78;
}

fieldset {
  --breakpoint:320px;
  opacity: 1;
  border: 0;
  margin: 0;
  padding: 0;
  display: flex;
  position: relative;
  z-index: 909;
  transition: opacity var(--ease-s) linear;
  animation: Fade var(--ease-s) ease-in 1 0s both;
}

fieldset:empty {
  display: none;
  opacity: 0;
  animation: Fade var(--ease-s) ease-in 1 0s reverse both;
}

label {
  --padding:calc( 1 * var(--pad));
  font-weight: 900;
  text-transform: uppercase;
  cursor: pointer;
  transition: color var(--ease-s) linear, background-color 90ms linear;
  padding: var(--padding);
}
label:hover, label:focus {
  color: #e7dbd5;
  background-color: #455b63;
  outline: none;
}

input[type=radio] {
  appearance: none;
}

.active button.person {
  display: block;
}

.no-faces #app-frame::after {
  content: "";
  position: absolute;
  top: 0;
  left: 0;
  right: 0;
  bottom: 0;
  background: url(assets/icons/child_care-24px.svg) no-repeat center center;
  background-size: contain;
  opacity: 1;
  transform: translateZ(100px);
  display: block;
  z-index: 303;
}

.failure #controls {
  display: none;
}
.failure #feedback {
  left: var(--pad);
  right: var(--pad);
  bottom: calc(50vh - 0.5rem);
  text-align: center;
  background-color: #8d3525;
  color: #e7dbd5;
  border: 10px solid #e7dbd5;
  font-size: 240%;
}
.failure #feedback::after {
  display: none;
}

[id] {
  scroll-margin-top: 2ex;
  scroll-snap-margin-top: 2ex;
}

button,
#shared-controls > * {
  --icon-size:32px;
  --fade-speed:var(--ease-q);
  --pos: left var(--pad) center;
  --col-top:$col-cream;
  --col-bottom: $col-grey;
  --direction:row;
  text-shadow: rgba(231, 219, 213, 0.4) 0 0px 2px, rgba(231, 219, 213, 0.3) 0 0.5px 2px, rgba(157, 130, 114, 0.2) 0 1px 2px, rgba(157, 130, 114, 0.1) 0 1.5px 2px;
  border-radius: 5px;
  border-style: double ridge outset groove;
  cursor: pointer;
  min-width: 44px;
  min-height: 44px;
  max-width: var(--max-width-override, var(--max-width, auto));
  position: relative;
  box-sizing: border-box;
  margin: 0;
  padding: var(--pad);
  white-space: pre;
  user-select: none;
  appearance: none;
  line-height: 1;
  font-size: clamp(8px, -0.5385rem + 2.2vmax, 14px);
  z-index: 1;
  display: flex;
  flex-direction: var(--dir-override, var(--direction, row));
  align-items: center;
  justify-content: space-between;
  background-color: #9d8272;
  background-image: var(--bg), url(./assets/textures/45-degree-fabric-light.png);
  background-repeat: no-repeat, repeat, repeat-x;
  background-size: auto var(--icon-size), auto, auto;
  background-position: var(--pos-override, var(--pos, center)), center, center;
  color: #262422;
  animation-name: unset;
  animation-fill-mode: both, both;
  animation-duration: var(--duration, 900ms), var(--duration, 900ms);
  transition: transform var(--fade-speed) cubic-bezier(0.175, 0.885, 0.32, 1.275), background-color var(--fade-speed) linear, color var(--fade-speed) linear;
}
button:empty,
#shared-controls > *:empty {
  display: none;
}
button:before,
#shared-controls > *:before {
  display: block;
  min-width: var(--icon-size);
  min-height: var(--icon-size);
  content: "";
  background: transparent var(--bg) no-repeat;
  background-position: top center;
  background-size: auto var(--icon-size);
  filter: brightness(0%);
  opacity: 1;
  transition: opacity var(--fade-speed) linear;
}
button:not(.hide-text):before,
#shared-controls > *:not(.hide-text):before {
  margin-right: calc(var(--pad) / 2 );
}
button:hover:before, button:focus:before,
#shared-controls > *:hover:before,
#shared-controls > *:focus:before {
  opacity: 0;
  z-index: 1;
}
button:active,
#shared-controls > *:active {
  background-color: #a45c1e;
}
button:focus,
#shared-controls > *:focus {
  outline: 4px solid #ab8f54;
}
button.hide,
#shared-controls > *.hide {
  display: none;
}

fieldset > *:nth-child(1) {
  animation-delay: calc( 0 * 45ms );
  animation-delay: calc( var(--offset) + 0 * var(--overlap) * var(--duration) );
}
fieldset > *:nth-child(2) {
  animation-delay: calc( 1 * 45ms );
  animation-delay: calc( var(--offset) + 1 * var(--overlap) * var(--duration) );
}
fieldset > *:nth-child(3) {
  animation-delay: calc( 2 * 45ms );
  animation-delay: calc( var(--offset) + 2 * var(--overlap) * var(--duration) );
}
fieldset > *:nth-child(4) {
  animation-delay: calc( 3 * 45ms );
  animation-delay: calc( var(--offset) + 3 * var(--overlap) * var(--duration) );
}
fieldset > *:nth-child(5) {
  animation-delay: calc( 4 * 45ms );
  animation-delay: calc( var(--offset) + 4 * var(--overlap) * var(--duration) );
}
fieldset > *:nth-child(6) {
  animation-delay: calc( 5 * 45ms );
  animation-delay: calc( var(--offset) + 5 * var(--overlap) * var(--duration) );
}
fieldset > *:nth-child(7) {
  animation-delay: calc( 6 * 45ms );
  animation-delay: calc( var(--offset) + 6 * var(--overlap) * var(--duration) );
}
fieldset > *:nth-child(8) {
  animation-delay: calc( 7 * 45ms );
  animation-delay: calc( var(--offset) + 7 * var(--overlap) * var(--duration) );
}

@media screen and (max-width: 1960px) {
  button .hide-text,
#shared-controls > * .hide-text {
    text-indent: -9999px;
    overflow: hidden;
    color: transparent;
    text-shadow: none;
    white-space: pre;
    font-size: 0;
    background-position: center center;
    justify-content: center;
  }
}
@media screen and (max-width: 1240px) {
  :root {
    --footer-gap:calc(74px);
  }

  button,
#shared-controls > * {
    --icon-size:26px;
    --pos:top var(--pad) center;
    --direction: column;
  }
  button:not(.hide-text):before,
#shared-controls > *:not(.hide-text):before {
    margin-right: 0;
  }
}
@media screen and (max-width: 586px) {
  :root {
    --footer-gap:calc(67px);
  }

  button,
#shared-controls > * {
    --icon-size:28px;
    --pos: center center;
    --direction: row;
    font-size: 0;
    text-indent: -9999px;
    overflow: hidden;
    color: transparent;
    justify-content: center;
  }
}
@media screen and (max-width: 388px) {
  :root {
    --footer-gap:calc(67px);
  }

  button,
#shared-controls > * {
